"use client";

import { useState } from "react";

export default function Home() {
  const [fontIndex, setFontIndex] = useState(0);

  const fonts = ["font-sans", "font-avenir", "font-montserrat"];

  const fontNames = ["Default", "Avenir", "Montserrat"];

  return (
    <div className="p-8">
      <button
        onClick={() => setFontIndex((fontIndex + 1) % fonts.length)}
        className="mb-4 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
      >
        Font Değiştir ({fontNames[fontIndex]})
      </button>

      <p className={`${fonts[fontIndex]} text-2xl`}>
        Bu metin {fontNames[fontIndex]} fontu ile yazılmıştır
      </p>
    </div>
  );
}
